.footer {
    padding-bottom: 62px;
    @media screen and (min-widt:768px) {
        padding-bottom: 13px;
    }
    @media screen and (min-widt:1200px) {
        padding-bottom: 11px;
    }
}
.footer__info-wrapper {
    position: relative;
    border-bottom: 1px solid #E1E1E1;
    padding-bottom: 174px;
    @media screen and (min-width:768px) {
        padding-bottom: 32px;
    }
    @media screen and (min-width:1200px) {
        padding-bottom: 54px;
    }
}
.footer__text {
    display: flex;
    flex-direction: column;
}
.footer__text--small {
    width: 240px;
    margin-bottom: 16px;
    font-weight: 700;
    font-size: 18px;
    line-height: 1.22;
    text-transform: uppercase;

    color: var(--primary-text-color);
    @media screen and (min-width:768px) {
        width: 352px;
        margin-bottom: 9px;
        font-size: 26px;
        line-height: 1.23;
    }
    @media screen and (min-width:1200px) {
        width: 504px;
        margin-bottom: 14px;
        font-style: normal;
        font-weight: 400;
        font-size: 30px;
        line-height: 1.23;
    }
}
.footer__text--large {
    width: 200px;
    font-weight: 700;
    font-size: 34px;
    line-height: 1.2;
    text-transform: uppercase;
    color: var(--primary-text-color);
    @media screen and (min-width:768px) {
        width: 340px;
        font-size: 48px;
        line-height: 1.23;
        letter-spacing: 0.06em;
    }
    @media screen and (min-width:1200px) {
        width: 500px;
        font-size: 58px;
        line-height: 1.22;
    }
}
.footer-contacts {
    position: absolute;
    bottom: 40px;
    right: 20px;
    display: flex;
    flex-direction: column;
    @media screen and (min-width:768px) {
        bottom: 56px;
    }
    @media screen and (min-width:1200px) {
        bottom: 60px;
    }
}
.footer-contacts__sm-list {
    display: flex;
    gap: 28px;
    margin-bottom: 38px;
    margin-left: auto;
    @media screen and (min-width:768px) {
        margin-bottom: 32px;
        gap: 30px;
    }
    @media screen and (min-width:1200px) {
        margin-bottom: 40px;
    }
}
.footer-contacts__sm-item {
    width: 32px;
    height: 32px;
    @media screen and (min-width: 768px) {
        width: 34px;
        height: 34px;
    }
}
.footer-contacts__sm-link {
    position: relative;
    border-radius: 50%;
    background-color: var (--accent-color);

}
.footer-contacts__sm-icon {
    width: 32px;
    height: 32px;
    position: absolute;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    fill: var(--title-item-text-color);
    @media screen and (min-width: 768px) {
        width: 34px;
        height: 34px;
        fill: var(--main-bgc-color);
    }
}
.footer-contacts__call-text {
    font-weight: 700;
    font-size: 12px;
    line-height: 1.86;
    text-align: right;
    color: var(--secondary-text-color);
    @media screen and (min-width:768px) {
        font-size: 14px;
    }
    @media screen and (min-width:1200px) {
        font-size: 16px;
    }
    }
.footer-contacts__call-number {
    color: var(--primary-text-color);
    }

.footer__copyright {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 16px;
    @media screen and (min-width:768px) {
        flex-direction: row;
        justify-content: center;
        margin-top: 14px;
    }
    @media screen and (min-width:1200px) {
        margin-top: 12px;
    }
    // border-top: 1px solid #E1E1E1;
}
.footer__copyright--text-wrapper {
    font-weight: 700;
    font-size: 14px;
    line-height: 1.52;    
    text-align: center;    
    color: #7A7A7A;
    @media screen and (min-widt:768px) {
        line-height: 1.86;
        letter-spacing: 0.02em;
    }
    @media screen and (min-widt:1200px) {
        font-size: 12px;
    }
    
}

